<template>
  <div id="app">
  <div class="background" :style="{backgroundImage: `url(${imgUrl})`}">
    <todoList></todoList>
  </div>
  </div>
</template>

<script>

import todoList from './components/todoList.vue'

const jsonp = require('jsonp');

export default {
  name: 'app',
  data(){
    return{
      imgUrl:""
    }
  },
  components: {
    todoList
  },
  created(){

      jsonp('https://bing.ioliu.cn/v1?d=1&w=1920&h=1200', null, (err, res) => {

      if (err) {
        console.error(err);
      } else {
        console.log(res.data.url);
        this.imgUrl = res.data.url;
      }

    });

  }
}
</script>

