<template>
  <div id="countDown">
  </div>
</template>

<script>
export default {
  name: 'countDown',
  data(){
    return{
    }
  },
  created(){
      // this.open();
  },
  methods: {
    open() {
      this.$notify({
        title: '♪(＾∀＾●)ﾉ',
        message: `距离去泰国泼水节玩耍还有${this.timeDown}天`,
        duration: 0,
        position: 'bottom-right'
      });
    }
  },
  computed: {
    timeDown: function() {
        let date = '2019-04-13';
        //结束时间
        let endDate = new Date(date).getTime();
        //当前时间
        let nowDate = new Date().getTime();
        //相差的总天数
        let days = parseInt((endDate - nowDate) / (1000 * 60 * 60 * 24));
        return days;
    }
  }
}
</script>

